* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  padding-top: 4vh;
  padding-bottom: 4vh;
}

.App {
  max-width: 728px;
  margin: 4rem auto;
}

.js-plotly-plot .plotly .main-svg {
  overflow: visible;
}

.js-plotly-plot .plotly .main-svg .legend {
  transform: translate(150px, 440px);
}
